body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  color: rgba(0, 0, 0, 0.6);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.body {
  height: 90vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header {
  margin-left: auto;
  margin-right: auto;
  width: 92vw;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#id {
  width: 10vw;
  margin-right: 50vw;
}

.column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.row {
  width: 90vw;
  display: flex;
  justify-content: space-between;
}

.row_no_space {
  width: 90vw;
  display: flex;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input {
  width: 40vw;
  margin: 4px
}

.blog {
  background-color: rgba(255, 165, 0, 0.07);
  border: 20px solid rgba(255, 165, 0, 0.01);
  border-radius: 25px;
  margin: 8px;
}

.description {
  width: 90vw
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
